.App {
  min-height: 80vh;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  margin-bottom: 2px;
  margin-right: 6px;
}

.datePickerContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  margin-top: 0px;
  margin-bottom: 2px;
}

.datePicker {
  box-sizing: border-box;
  max-width: 90px;
}

.boxes {
  display: block;
  box-sizing: border-box;
  font-size: 4px;
  line-height: 4px;
  /* margin-bottom: 2px; */
  /* height: 6px; */
  /* margin: 3px; */
  /* line-height: 5px; */
  padding: 0;
  margin: 0;
  margin-bottom: 1px;
}

.box {
  display: inline-block;
  box-sizing: border-box;
  line-height: 8px;
  font-size: 4px;
  height: 4px;
  width: 4px;
  /* line-height: 12px; */
  /* padding: 0; */
  margin-right: 1px;
  border: 1px solid #444;
}

.filled {
  background-color: red;
  border-color: red;
}

.noBox {
  border: none;
}

.footer1 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.footer2 {
  margin-top: 4px;
  border-top: 1px solid #ccc;
  font-size: 12px;
  text-align: right;
}

.key {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.keyItem {
  font-size: 12px;
}
